@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root {
  --font-family-default: 'Poppins', sans-serif;

  --clr-background-1: 0, 0%, 100%;
  --clr-background-2: 210, 22%, 89%;
  --clr-background-3: 0, 0%, 0%;
  --clr-primary: 209, 100%, 25%; /* dark-blue */
  --clr-secondary: 206, 25%, 80%; /* light-blue */
  --clr-white: 0, 0%, 100%;
  --clr-gold: 37, 100%, 62%;

  --circle-gold: 39, 75%, 47%;
  --circle-turquoise: 198, 64%, 36%;
  --circle-green: 102, 23%, 46%;
  --circle-turquoise: 198, 82%, 29%;
  --circle-green: 100, 39%, 48%;
  --circle-red: 5, 53%, 47%;

  --fs-100: .5rem;
  --fs-200: .875rem;
  --fs-300: 1rem;
  --fs-400: 1.125rem;
  --fs-500: 1.25rem;
  --fs-600: 1.5rem;
  --fs-700: 1.75rem;
  --fs-800: 2.5rem;
  --fs-900: 6rem;

  --carousel-width: 400px;
  --carousel-height: 300px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  scroll-behavior: smooth;
}

body { 
  position: relative;
  overscroll-behavior: none;
  background-color: hsl(var(--clr-white));
  color: hsl(var(--clr-primary));
  font-family: var(--font-family-default);
  font-size: var(--fs-400);
  font-weight: 400;
}

section {
  position: relative;
  width: 100%;
  padding: 2rem;
}

.section-header {
  margin-bottom: 2rem;
  font-size: var(--fs-800);
  font-weight: 600;
}

.back-btn {
  position: relative;
  display: flex;
  align-items: center;
  align-self: flex-start;
  position: fixed;
  top: 10.4rem;
  left: 1rem;
  cursor: pointer;
  z-index: 20;
}

.back-btn-icon {
  font-size: 1.7rem;
}

.single-page-layout {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding: 10rem 3rem;
}

.single-page-content {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 1400px;
}

.single-page-header {
  align-self: flex-start;
}

.copy {
  line-height: 1.3;
  max-width: 800px;
  margin: 0 0 1rem 0 ;
}

.block {
  font-size: 1.5rem;
  font-weight: 500;
  font-style: italic;
  padding: 1.5rem;
  color: hsl(var(--clr-white));
  border-radius: .4rem;
  margin: 1rem 0 2rem;
  box-shadow: 6px 6px 10px hsl(var(--clr-primary), .4);
}

.btn-hover:hover {
  transform: scale(.98);
  box-shadow: inset 3px 3px 10px hsl(var(--clr-background-3), .4);
}

.red-background {
  background-color: hsl(var(--circle-red), 1);
}

.yellow-background {
  background-color: hsl(var(--circle-gold), 1);
}

.turquoise-background {
  background-color: hsl(var(--circle-turquoise), .8);
}

.green-background {
  background-color: hsl(var(--circle-green), .8);
}

.navy-background {
  background-color: hsl(var(--clr-primary), .8);
}

.red-border {
  border: 2px solid hsl(var(--circle-red), .8);
  color: hsl(var(--circle-red), .8);
  /* box-shadow: 6px 6px 10px hsl(var(--circle-red), .4); */
}

.yellow-border {
  border: 2px solid hsl(var(--circle-gold), .8);
  color: hsl(var(--circle-gold), .8);
  /* box-shadow: 6px 6px 10px hsl(var(--circle-gold), .4); */
}

.green-border {
  border: 2px solid hsl(var(--circle-green), 9);
  color: hsl(var(--circle-green), .9);
  /* box-shadow: 6px 6px 10px hsl(var(--circle-green), .4); */
}

.navy-border {
  border: 2px solid hsl(var(--clr-primary), .8);
  color: hsl(var(--clr-primary), .8);
  /* box-shadow: 6px 6px 10px hsl(var(--clr-primary), .4); */
}

.turquoise-border {
  border: 2px solid hsl(var(--circle-turquoise), .8);
  color: hsl(var(--circle-turquoise), .8);
  /* box-shadow: 6px 6px 10px hsl(var(--circle-turquoise), .4); */
}

.position-left {
  align-self: flex-start;
}

.position-right {
  align-self: flex-end;
}

.position-center {
  align-self: center;
}

.cta-btn {
  background-color: hsl(var(--clr-primary));
  color: hsl(var(--clr-white));
  border: none;
  border-radius: .7rem;
  padding: 1rem 2rem;
  font-size: var(--fs-400);
  font-weight: 300;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.program-btn {
  margin-top: 2rem;
}

.master-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

/* INTRO SECTION */

.intro-section {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: hsl(var(--clr-background-2));
  height: 100vh;
}

.intro-img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}

.intro-header {
  position: absolute;
  top: 25%;
  left: 31%;
  color: hsl(var(--clr-white));
  font-size: 7vw;
  font-weight: 600;
  text-shadow: 4px 4px 9px #969696;
}

.intro-copy-container {
  position: absolute;
  top: 66%;
  left: 45%;
  display: flex;
  align-items: center;
  width: 50%;
}

.intro-line-graphic {
  background-color: hsl(var(--clr-primary));
  height: 3px;
  width: 30rem;
}

.intro-copy {
  margin-left: 3rem;
  line-height: 1.2;
  max-width: 24rem;
}

.intro-cta {
  position: absolute;
  left: 5%;
  bottom: 5%;
  font-size: 1.3rem;
  z-index: 2;
}

/* APPROACH SECTION */

.approach-section {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 6rem;
}

.approach-content {
  height: 900px;
}

.client-overlap-header {
  position: absolute;
  top: 22.5rem;
  left: -.8rem;
  z-index: 2;
  text-align: center;
  color: hsl(var(--clr-white));
  font-weight: 300;
  font-size: 1.6rem;
  filter: drop-shadow(14px 20px 2.5px hsla(0, 0%, 16%, 0.416));
}

.approach-graphic {
  position: relative;
}

.approach-circle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  color: hsl(var(--clr-white));
  transition: all .3s ease-in-out;
}

.approach-circle.green {
  top: 2rem;
  left: -27rem;
  background: linear-gradient(hsl(101, 64%, 67%) 0%, hsla(100, 28%, 51%, 0.7) 10%, hsla(102, 40%, 33%, 0.7) 50%, hsla(99, 27%, 15%, 0.85) 100%);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.approach-circle.green::before{
  content: "";
  height: 460px;
  width: 460px;
  background: linear-gradient(hsla(84, 77%, 73%, 1) 0%,  hsla(100, 39%, 48%, 0.7) 60%,  hsla(99, 31%, 15%, 0.85) 100%);
  border-radius: 50%;
  filter: blur(18px);
}

/* .approach-circle.green:hover {
  background-color: hsl(102, 23%, 46%, 1);
  z-index: 10;
  top: 2.5rem;
  transform: scale(1.05);
} */

.approach-circle.gold {
  top: 2rem;
  left: -1rem;
  background: linear-gradient(hsl(55, 76%, 74%, 1) 0%, hsla(39, 69%, 55%, 0.7) 10%, hsla(39, 82%, 36%, 0.7) 50%, hsla(37, 90%, 12%, 0.85) 100%);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.approach-circle.gold::before{
  content: "";
  height: 460px;
  width: 460px;
  background: linear-gradient(hsla(50, 80%, 78%, 1) 0%, hsla(39, 83%, 51%, 0.7) 60%, hsla(37, 82%, 24%, 0.85)  100%);
  border-radius: 50%;
  filter: blur(18px);
}

/* .approach-circle.gold:hover {
  background-color:  hsla(39, 69%, 55%, 0.7);
  z-index: 10;
  top: 2.5rem;
  transform: scale(1.05);
} */

.approach-circle.turquoise {
  top: 22rem;
  left: calc(-27rem/2);
  background: linear-gradient(hsl(198, 78%, 75%) 0%, hsla(198, 90%, 68%, 0.7) 10%, hsla(198, 81%, 27%, .7) 50%, hsla(198, 82%, 20%, 0.85) 100%);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.approach-circle.turquoise::before{
  content: "";
  height: 460px;
  width: 460px;
  background: linear-gradient(hsla(198, 64%, 56%, 1)  0%, hsla(198, 82%, 29%, 0.7) 60%,hsla(198, 80%, 10%, 0.85)  100%);
  border-radius: 50%;
  filter: blur(18px);
}

/* .approach-circle.turquoise:hover {
  background-color:  hsla(198, 82%, 20%, 0.866) ;
  z-index: 10;
  top: 22.5rem;;
} */

.approach-circle.green.shadow,
.approach-circle.shadow,
.approach-circle.turquoise.shadow {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  z-index: -2;
  box-shadow: 20px 20px 30px #5b5d5e80;
}

.approach-circle-header {
  position: absolute;
  top: 8rem;
  max-width: 220px;
  margin-bottom: 2rem;
  font-weight: 200;
  font-size: 1.6rem;
  text-align: center;
}

.approach-circle-copy {
  position: absolute;
  top: 14rem;
  width: 320px;
  font-weight: 300;
  text-align: center;
  transition: all .3s ease-in-out;
}

/* PROGRAMS HOME SECTION */

.programs-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 4rem;
  margin-bottom: 6rem;
  background-color: hsl(var(--clr-gold), .3);
}

.programs-intro {
  text-align: center;
}

.programs-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1400px;
  margin: 3rem 2rem;
}

.program-container {
  display: flex;
  margin-bottom: 2rem;
}

.program-container:nth-child(odd) {
  flex-direction: row-reverse;
}

.programs-img {
  width: 300px;
  object-fit: cover;
}

.programs-img-1,
.programs-img-3,
.programs-img-5 {
  border-top-right-radius: 20% 50%;
  border-bottom-right-radius: 20% 50%;
  object-position: 100% 0;
}

.programs-img-2,
.programs-img-4 {
  border-top-left-radius: 20% 50%;
  border-bottom-left-radius: 20% 50%;
  object-position: 100%;
}

.program-1 .programs-copy,
.program-3 .programs-copy,
.program-5 .programs-copy {
  padding: 1rem 1.5rem 2rem 4rem;
}

.program-2 .programs-copy,
.program-4 .programs-copy {
  padding: 1rem 4rem 2rem 1.5rem;
}

.program-1,
.program-3,
.program-5,
.program-1 .programs-subhead,
.program-3 .programs-subhead,
.program-5 .programs-subhead {
  text-align: right;
}

.programs-img-1 {
  object-position: 75%;
}

.programs-img-2,
.programs-img-5 {
  object-position: 50%;
}

.programs-img-4 {
  object-position: 100%;
}

.programs-subhead {
  font-weight: 600;
  font-size: 1.25rem;
  text-align: left;
  margin-bottom: 1rem;
}

.programs-copy {  
  max-width: 35rem;
  transition: all .3s ease-in-out;
  background-color: hsl(var(--clr-white), .7);
}

.programs-copy.copy {
  margin-bottom: 0;
}

.program-1 {
  align-self: flex-start;
}

.program-2,
.program-5  {
  align-self: flex-end;
}

.program-3 {
  align-self: center;
}

/* RIGHTS HOME BANNER */

.rights-banner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  padding: 1rem;
  font-size: 2rem;
  border-top: 2px solid hsl(var(--circle-gold));
  border-bottom: 2px solid hsl(var(--circle-gold));
  color: hsl(var(--circle-gold));
}

.rights-banner > * {
  margin: 0 4rem;
}

.rights-header {
  color: hsl(var(--circle-gold))
}

.rights-cta {
  background-color: hsl(var(--circle-gold));
  color: hsl(var(--clr-white));
  border: none;
  border-radius: .7rem;
  padding: 1rem 2rem;
  font-size: 1.8rem;
  font-weight: 300;
  cursor: pointer;
}

/* NEWS HOMEPAGE SECTION */

.news-section {
  display: relative;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 6rem;
}


.news-carousel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5rem 0 7rem;
  overflow: hidden;
}

.carousel-arrow {
  font-size: 2.5rem;
}

.carousel-item-container {
  display: flex;
  flex-direction: column;
  width: 20vw;
  height: 550px;
  text-align: left;
  font-size: 1rem;
}

.carousel-item-container > * {
  margin-bottom: 1rem;
  color: hsl(var(--clr-primary));
}

.carousel-item-img {
  width: 100%;
  height: 20vw;
  object-fit: cover;
  border-radius: .8rem;
}

.carousel-item-header {
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 1.1;
  margin: 1rem 0 ;
}

.carousel-item-summary {
  line-height: 1.3;
}

.carousel-item-readmore {
  display: flex;
  margin-top: auto;
  margin-bottom: 0;
  text-align: right;
  justify-content: flex-end;
}

.carousel-item-readmore p {
  margin-right: .5rem;
}


.carousel-item-readmore:hover {
  text-decoration: underline;
  text-underline-offset: 3px;
}

.news-footer {
  display: flex;
  justify-content: space-between;
  margin-top: auto;
  padding: 0 6rem;
}

.news-social-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 350px;
}

.news-social-copy {
  font-size: 1.1rem;
  font-weight: 300;
}

.news-social-icons {
  font-size: 1.6rem;
  color: hsl(var(--clr-primary));
  margin-left: .4rem;
}

.news-viewall-btn {
  background-color: hsl(var(--clr-primary));
  padding: .6rem 1rem;
  border: none;
  border-radius: .5rem;
  font-size: 1rem;
  color: hsl(var(--clr-white));
}

/* NEWS ALL ARTICLES PAGE*/

.article-container {
  display: flex;
  width: 100%;
  height: 200px;
  border-radius: .4rem;
  margin-bottom: 1.5rem;
}

.article-container .article-img {
  width: 200px;
  height: 200px;
  object-fit: cover;
}

.article-copy {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 500px;
  margin-left: 1rem;
  color: hsl(var(--clr-primary));
}


/* NEWS INDIVIDUAL ARTICLE PAGE*/

.article-detail-copy {
  max-width: 70%;
}

.article-detail-copy img {
  height: 200px;
  width: 200px;
  object-fit: cover;
}

/* CONTACT SECTION */
.contact-section {
  position: relative;
  background-color: #979a98;
  color: hsl(var(--clr-white));
  padding: 6rem;
}

.contact-content {
  position: relative;
  display: flex;
  justify-content: space-evenly;
  background-color: #343836;
  border-radius: .7rem;
}

.contact-content-left,
.contact-content-right {
  width: 45%;
  padding: 3rem 2rem;
}

.contact-map-img {
  width: 100%;
  margin: 1.5rem 0 2rem;
  border-radius: .7rem;
}

.contact-copy {
  margin-bottom: 1rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: .4rem;
  margin-top: 2rem;
}

.contact-form input,
.contact-form textarea {
  font-family: var(--font-family-default);
  font-size: var(--fs-400);
  line-height: 1.2;
  color: #343836;
}

.select-label {
  display: flex;
  flex-direction: column;
}

.contact-form select {
  font-family: var(--font-family-default);
  font-size: 1.1rem;
  color: #343836;
  font-weight: 400;
  margin-top: 1rem;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
}

.contact-form select::placeholder,
.contact-form input::placeholder,
.contact-form textarea::placeholder {
  font-family: var(--font-family-default);
  font-size: var(--fs-400);
  line-height: 1.2;
  color: #343836;
  font-weight: 300;
  padding-top: .5rem;
}

.contact-form input:focus, 
.contact-form select:focus, 
.contact-form textarea:focus {
  outline: none;
} 

.contact-form input, 
.contact-form select {
  min-width: 100%;
  background-color: hsl(var(--clr-white));
  padding: .7rem 1rem;
  margin-bottom: 1.5rem;
  border: none;
  border-radius: .3rem;
}

.contact-form input.input-error,
.contact-form select.input-error {
  border: 1.5px solid #e10000;
  border-radius: .5rem;
}

.contact-form textarea {
  min-width: 100%;
  max-width: 100%;
  height: 227px;
  padding: 1rem;
  margin: 0 0 2.5rem;
  background-color: hsl(var(--clr-white));
  border: none;
  border-radius: .4rem;
  resize: none;
}

.contact-form textarea.input-error {
  border: 1.5px solid #e10000;
}

.contact-btn {
  align-self: flex-start;
  background-color: hsl(var(--clr-white));
  color: #343836;
  border: none;
  border-radius: .7rem;
  padding: 1rem 2rem;
  font-size: 1.1rem;
  cursor: pointer;
}

.success-popup {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 40rem;
  padding: 2rem;
  background-color: hsl(var(--clr-accent), .4);
  border-radius: .4rem;
  color:hsl(var(--clr-white));
  text-align: center;
  opacity: 0;
  top: -200px;
  transform: translate(-50%, -50%) scale(0.5);
  transition: opacity 300ms ease-in-out,
              top 500ms ease-in-out,
              transform 500ms ease-in-out;
}

.checkmark-background {
  position: relative;
  width: 3rem;
  height: 3rem;
  background-color: #fff;
  border-radius: 50%;
}

.success-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  font-size: var(--fs-900);
  color: hsl(var(--clr-accent));
}

.success-message {
  color: hsl(var(--clr-accent));
}

.success-thanks {
  margin-bottom: .8rem;
}

.success-btn {
  margin-top: 3.5rem;
}

/* FOOTER */


.footer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: auto;
  width: 100%;
  background-color: #000;
  padding: 3rem 6rem 1rem;
}

.footer-content {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-bottom: 4rem;
  z-index: 3;
}

.footer-content {
  width: 80%;
}

.footer-category {
  margin-bottom: .6rem;
  color: hsl(var(--clr-white));
  font-size: .9rem;
  font-weight: 200;
}

.footer-content ul > a {
  margin-bottom: .6rem;
  color: hsl(var(--clr-white));
  font-size: .9rem;
  font-weight: 200;
  margin-left: 1rem;
}

.footer-left,
.footer-right {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.footer-left-top,
.footer-left-middle,
.footer-left-bottom,
.footer-right-top,
.footer-right-middle {
  display: flex;
  flex-direction: column;
}

.footer-right-bottom {
  display: flex;
  justify-content: right;
}

.footer-right-bottom a img {
  height: 20px;
  width: 20px;
  margin: 5px 0 0 10px;
}

.footer-right {
  text-align: right;
}

.footer a {
  transition: all .2s ease-in-out;
}

.footer a:hover {
  color: #969696;
}

.footer-social-icons {
  font-size: 1.5rem;
  color: hsl(var(--clr-white));
  margin-left: .4rem;
  transition: all .2s ease-in-out;
}

.footer-social-icons:hover {
  color: #969696;
}

.copyright {
  color: hsl(var(--clr-white));
  font-size: .9rem;
  font-weight: 200;
}

/* STAFF PAGE */
.staff-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.staff-main-header,
.staff-copy {
  text-align: center;
}

.staff-main-header {
  align-self: center;
  max-width: 700px;
}

.staff-copy {
  max-width: 70%;
  line-height: 1.2;
  align-self: center;
}

.offices-container {
  position: relative;
  width: 85%;
  border: 2.5px solid hsl(var(--clr-primary));
  margin: 3rem 0 ;
  background-color: hsl(var(--clr-white));
  border-radius: .5rem;
  z-index: 10;
  box-shadow: 10px 10px 30px hsl(var(--clr-primary), .2);
}

.office-content {
  animation: growDown 500ms ease-in-out;
  animation-fill-mode: forwards;
  transform-origin: top center;
  line-height: 1;
  margin: 0 1rem;
}

@keyframes growDown {
  0% {
      transform: scaleY(0)
  }
  80% {
      transform: scaleY(1.1)
  }
  100% {
      transform: scaleY(1)
  }
}

.office {
  padding: .5rem 1rem;
  border-bottom: 2.5px solid hsl(var(--clr-primary));
  width: 100%;
  font-weight: 300;
}

.office-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.office:last-child {
  border-bottom: none;
}

.office-name {
  font-size: 1.4rem;
}

.office-address {
  max-width: 280px;
}

.office-phone {
  margin: .5rem 0;
}

.staff-profiles .empty-office-message {
  padding: 3rem 0 4rem;
  font-size: 1.5rem;
}

.staff-profiles {
  display: flex;
  flex-direction: column;
  min-height: 150px;
  max-height: 650px;
  overflow-x: scroll;
  /* display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  grid-gap: 2rem 1rem; */
  margin: 2rem 0;
}

.staff-profile {
  display: flex;
  width: 100%;
  min-height: 300px;
  /* display: grid;
  grid-template-columns: 1fr 2.5fr;
  gap: 1rem;
  max-width: 320px; */
}

.staff-img-container {
  /* grid-column: 1;
  grid-row: 1; */
  max-width: 165px;
  padding: 0 1rem;
}

.staff-info-container {
  display: flex;
  flex-direction: column;
  height: 164px;
}

.staff-img {
  width: 130px;
  height: 130px;
  object-fit: cover;
  border-radius: .8rem;
  margin-bottom: .7rem;
}

.staff-firstname,
.staff-lastname {
  font-size: 1.8rem;
}

.staff-title {
  max-width: 10rem;
  margin: .5rem 0;
}

.staff-email, 
.staff-phone {
  display: flex;
  align-items: center;
}

.staff-email {
  font-size: .92rem;
  margin-top: auto;
}

.staff-email.small {
  font-size: .78rem;
  letter-spacing: -.5px;
}

.small .staff-email-icon {
  font-size: .92rem;
}

.staff-email-icon, 
.staff-phone-icon {
  margin-right: .7rem;
}

.staff-phone {
  margin-top: .5rem;
}

.staff-bio {
  grid-column: span 2;
  padding: 0 2rem;
}

.toggle-office-icon {
  font-size: 2.8rem;
  padding: .5rem;
  transform: rotate(0deg);
  transition: transform .3s linear;
}

.toggle-office-icon.open{
  transform: rotate(180deg);
  transition: transform .3s linear;
}

.show-bio-btn {
  width: 130px;
  padding: .3rem;
  background-color: hsl(var(--clr-primary));
  font-size: .9rem;
  text-align: center;
  color: hsl(var(--clr-white));
  border-radius: .3rem;
  cursor: pointer;
  transition: all .2s ease-in-out;
}

/* LEGAL SERVICES */

.courthouse-img {
  width: 55%;
  border-radius: .5rem;
  float: left;
  margin-right: 2rem;
}


@media screen and (max-width: 800px) {
  section {
    padding: 0rem;
  }

  .back-btn {
   position: absolute;
   top: auto;
   bottom: 2rem;
  }

  .intro-section,
  .approach-section,
  .contact-section,
  .news-section,
  .staff-section {
    padding: 2rem;
  }

  .single-page-layout {
    padding: 6rem 2rem;
  }

  .copy {
    max-width: 100%;
  }

  .intro-img {
    width: 100%;
    object-fit: cover;
  }

  .intro-header {
    left: 0;
    font-size: 4rem;
    text-shadow: 4px 4px 9px #5a5858;
    margin-left: 2rem;
  }

  .intro-copy-container {
    color: hsl(var(--clr-white));
    text-shadow: 4px 4px 9px #5a5858;
    top: 50%;
    left: 0;
    display: flex;
    align-items: center;
    width: 100%;
  }

  .intro-line-graphic {
    display: none;
  }

  .intro-copy {
    margin-left: 2rem;
    max-width: 24rem;
  }

  .approach-content {
    height: 700px;
  }
  
  .client-overlap-header {
    position: absolute;
    top: 12.5rem;
    left: -2.4rem;
    z-index: 2;
    text-align: center;
    color: hsl(var(--clr-white));
    font-weight: 300;
    font-size: 1.6rem;
    filter: drop-shadow(14px 20px 2.5px hsla(0, 0%, 16%, 0.416));
  }

  .approach-circle {
    width: 53vw;
    height: 53vw;
    border-radius: 50%;
  }

  .approach-circle.green {
    top: 2rem;
    left: -14rem;
  }

  .approach-circle.gold {
    top: 2rem;
    left: -3rem;
  }

  .approach-circle.turquoise {
    top: 14rem;
    left: calc(-16rem/2);
  }

  .approach-circle.turquoise:hover {
    background-color:  hsla(198, 82%, 20%, 0.866) ;
    z-index: 10;
    top: 14.5rem;
    transform: scale(1.05);
  }

  .approach-circle.green::before,
  .approach-circle.gold::before,  
  .approach-circle.turquoise::before {
    height: 49vw;
    width: 49vw;
  }

  .approach-circle.green.shadow,
  .approach-circle.shadow,
  .approach-circle.turquoise.shadow {
    width: 53vw;
    height: 53vw;
  }

  .approach-circle-header {
    position: absolute;
    top: 40%;
    max-width: 210px;
    font-size: 1.2rem;
  }

  .approach-circle-copy {
    display: none;
    top: 7rem;
    width: 250px;
    font-size: 1rem;
  }

  .programs-section {
    padding: 2rem;
  }

  .program-container {
    flex-direction: column;
  }

  .program-container:nth-child(odd) {
    flex-direction: column;
  }

  .programs-img-1,
  .programs-img-2,
  .programs-img-3,
  .programs-img-4,
  .programs-img-5 {
    width: 100%;
    border-radius: 0;
  }

  .program-1 .programs-copy,
  .program-2 .programs-copy,
  .program-3 .programs-copy,
  .program-4 .programs-copy
  .program-5 .programs-copy {
    padding: 1rem;
  }

  .rights-banner {
    flex-direction: column;
    font-size: 1.3rem;
    white-space:nowrap;
  }

  .rights-banner > * {
    margin: .3rem 0;
  }

  .rights-cta {
    padding: 1rem;
    font-size: 1.2rem;
    font-weight: 300;
    cursor: pointer;
  }

  .news-carousel {
    margin: 2rem 0;
    justify-content: center;
  }

  .carousel-arrow {
    font-size: 2rem;
  }

  .carousel-item-container {
    width: 100%;
    text-align: left;
    font-size: 1rem;
    margin: 0 2rem
  }

  .carousel-item-img {
    height: auto;
  }

  .news-footer {
    padding: 0rem;
  }

  .news-social-links {
    justify-content: center;
    width: 100%;
  }

  .news-social-copy {
    max-width: 50%;
  }

  .contact-content {
    flex-direction: column;
  }

  .contact-content-left,
  .contact-content-right {
    width: 100%;
    padding: 1rem 2rem;
  }

  .footer-content {
    flex-direction: column;
  }

  .footer-content > * {
    align-self: center;
  }

  .footer-content .footer-right a {
    margin-left: 0;
  }

  .footer-right {
    text-align: left;
  }

  .footer-right > * {
    margin: 0;
  }

  .footer-right-bottom {
    justify-content: center;
  }

  .staff-copy {
    text-align: left;
  }

  .offices-container {
    width: 100%;
  }

  .office {
    padding: .5rem;
  }

  .office-name {
    font-size: 1.2rem;
    max-width: 80%;
  }

  .staff-profiles {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 2rem 1rem;
    margin: 2rem 0;
  }
}



