/* SECTION GRAPHICS PLACEMENT */


.section-graphic {
  position: absolute;
}

.intro-right {
  right: 0;
  top: 0;
  height: 120%;
}

.intro-bottom {
  bottom: 0;
  height: 95%;
  z-index: 2;
}

.approach-right {
  top: 3rem;
  right: 0;
  height: 65%;
  z-index: -10;
}

.approach-left {
  bottom: 3rem;
  left: 0;
  height: 50%;
  z-index: -10;
}

.programs-right {
  left: 1rem;
  bottom: 2rem;
  height: 27rem;
}

.programs-left {
  height: 30rem;
  top: 36rem;
  left: -10rem;
}

.programs-right-bottom {
  bottom: 20rem;
  right: 0rem;
  height: 90rem;
}

.programs-bottom {
  top: 10rem;
  right: -14rem;
  height: 25rem;
}

.news-graphic-home {
  position: absolute;
  bottom: 0;
  right: -10rem;
  width: 100rem;
  opacity: .4;
 z-index: -1;
}

.contact-graphic-left {
  left: 0;
  top: 0;
  height: 80rem;
  z-index: 0;
}

.contact-graphic-right {
  right: 0;
  top: 0;
  height: 80rem;
  z-index: 0;
}

.footer-graphic-left {
  bottom: 0;
  left: 0;
  height: 130%;
  z-index: 0;
}

.footer-graphic-right {
  bottom: 0;
  right: 0;
  height: 50%;
  z-index: 0;
}


/* SINGLE PAGE GRAPHICS PLACEMENT */

.single-page-graphic {
  position: absolute;
}

.legal-graphic-left {
  top: 15rem;
  left: 0;
  height: 35vw;
}

.legal-graphic-right {
  right: -10rem;
  bottom: 0;
  height: 30vw;
}

.staff-graphic-bottom {
  bottom: 0rem;
  left: -5rem;
  height: 35rem;
}

.staff-graphic_right {
  right: 0;
  height: 43rem;
}

@media screen and (max-width: 800px) {

  .programs-right {
    top: 17rem;
    right: -4rem;
    height: 30rem;
  }


  .contact-graphic-left {
    left: 0;
    top: 30%;
    height: 90rem;
    z-index: 0;
  }
  
  .contact-graphic-right {
    right: -20rem;
    top: -2rem;
    height: 80rem;
    z-index: 0;
  }
}